body{
    background-color:#a9d6e5;
    text-align: center;
}


.iconLuna{
    color:orange;
}

.TituloRyM{
    width:350px;
    filter:brightness(1.3);
    mix-blend-mode:multiply;
    list-style: none;
    text-decoration: none;
}

/* CARDS  */

.cardMorty{
    box-shadow: 5px 5px 25px;
    border-radius: 425px;
    background-color: #22577a;
}
.imgCard{
    border-radius:525px;
}

.imgCardFav{
    width:120px;
    border-radius:525px;
}

.textCard{
    font-family: 'Kalam', cursive;
    color:#caf0f8;
}

/* BOTÃ“N DE FAVORITOS */
.btnCorazon{
    color: #caf0f8;
}

.btnCorazon:hover{
    color: #d00000;
}
 .btnFav{
    background-color:ff0000;
    border-radius: 25px;
    border-style: none;
}

.btnFav :hover{
    background-color:#98c1d9;
    border-radius: 25px;
    border-style: none;
}


/* BUSCADOR */
.SearchRyM{
    border-radius: 25px;
    border-style: none;
    user-select: none;
    outline:none;
    width:45% !important;
    height:30px;
}